.main {
    display: flex;
    justify-content: space-between;
    height: 100vh;
}

.leftSide {
    width: 30%;
    background: #FFFFFF;
    border-right: 1px solid #D9DCE0;
    height: 100%;
}

.leftTop {
    display: flex;
}

.leftTopBurgerMenu {
    margin-top: 21px;
    margin-left: 26px;
    height: 14px;
    width: 19.997783660888672px;
}

.leftTopBurgerInput {
    display: flex;
    width: 276px;
    height: 40px;
    background: #F5F5F5;
    border-radius: 22px;
    margin-top: 8px;
    margin-left: 26px;
}

.leftTopBurgerInput img {
    height: 18.4989070892334px;
    width: 18.498300552368164px;
    margin-top: 10.5px;
    margin-left: 18.5px;
}

.leftTopBurgerInput input {
    margin-left: 19px;
    height: 40px;
    padding: 0px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #707991;
    border: none;
    outline: none;
    background: #F5F5F5;
    border-radius: 0px 22px 22px 0px;
}

.leftBottom {
    margin-top: 10px;
}

.leftRender {
    display: flex;
    justify-content: space-between;
    transition: all 0.3s ease;
    cursor: pointer;
}

.leftRender:hover {
    background-color: #F5F5F5;
}

.leftRenderLeft {
    display: flex;
}

.leftRenderLeft img {
    width: 48px;
    height: 48px;
    border-radius: 100px;
    margin-left: 16px;
    margin-top: 12px;
    margin-bottom: 12px;
}

.chatInfo {
    margin-left: 16px;
    margin-top: 15px;
}

.chatInfo h1 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #011627;
    display: flex;
}

.chatInfo h1 img {
    height: 18.36423110961914px;
    width: 18.388826370239258px;
    margin-left: 6px;
    margin-top: 1px;
}

.chatInfo p {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #707991;
    margin-top: -5px;
}

.leftRenderRight {
    margin-right: 16px;
}

.lastMessageSendedTime {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #707991;
    margin-top: 17px;
}

.countOfNewMessages {
    margin-top: 8px;
    padding: 3px;
    background: #78E378;
    border-radius: 100px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    color: #FFFFFF;
}














.rightSide {
    width: 70%;
    background-image: url(../../Images/bg.png);
    background-position: center;
    height: 100%;
}

.rightTop {
    height: 56px;
    background-color: white;
    display: flex;
    justify-content: space-between;
}

.rightBottom {
    width: 70%;
    margin-left: 15%;
}

.rightTopLeft {
    margin-left: 16px;
    display: flex;
}

.rightTopLeft div {
    margin-left: 16px;
}

.rightTopLeft div h1 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #011627;
    margin-top: 7px;
}

.rightTopLeft div p {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #707991;
}

.RightSearch {
    height: 18.4989070892334px;
    width: 18.498300552368164px;
    left: 2.500396728515625px;
    top: 2.5px;
    border-radius: 0px;
    margin-top: 18px;
    margin-right: 32px;
}

.RightOption {
    height: 16px;
    width: 4px;
    border-radius: 0px;
    margin-top: 20px;
}

.rightTopLeft img {
    width: 40px;
    height: 40px;
    border-radius: 100px;
    margin-top: 8px;
}

.rightTopRight {
    margin-right: 16px;
    display: flex;
}


.renderMessages {
    overflow: auto;
}



.sendMessage {
    width: 100%;
    height: 56px;
    background: #FFFFFF;
    border-radius: 12px;
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
}

.sendSmile {
    width: 10%;
    border: none;
    height: 56px;
    background: #FFFFFF;
    border-radius: 12px 0px 0px 12px;
}

.sendButton {
    width: 10%;
    border: none;
    height: 56px;
    background: #FFFFFF;
    border-radius: 0px 12px 12px 0px;
}

.sendText {
    outline: none;
    background-color: #FFFFFF;
    border: none;
    height: 56px;
    width: 80%;
}




.otherMessage {
    width: 100%;
    height: max-content;
    display: flex;
    justify-content: start;
}

.myMessage {
    width: 100%;
    height: max-content;
    display: flex;
    justify-content: end;
}

.otherMessage div {
    background-color: #ffffff;
}

.myMessage div {
    background-color: #78E378;
}

.message {
    margin-top: 16px;
    border-radius: 12px;
}

.messageText {
    padding: 4px 12px 5px 12px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #011627;
}

.messageTime {
    width: 100%;
    display: flex;
    justify-content: end;
    margin-bottom: 4px;
}

.messageTime p {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    margin-right: 8px;
    padding-left: 5px;
}

.lineMessage {
    height: 1px;
    width: 100%;
    background: linear-gradient(90deg, rgba(127, 127, 127, 0) 0%, rgba(127, 127, 127, 0.4) 2%, rgba(127, 127, 127, 0.4) 98%, rgba(127, 127, 127, 0) 100%);
    border-radius: 300px;
}

.backButton {
    background-color: white;
    border: none;
    width: max-content;
    height: max-content;
    cursor: pointer;
}

.backButton img {
    height: 30px;
    width: 30px;
    margin-top: 13px;
    margin-right: 5px;
}